.meals-page {
    padding: 20px;
    background-color: #f8f8f8;
  }
  
  .meals-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px;
  }
  
  .meal-card {
    background: #fff;
    padding: 20px;
    margin: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
    width: 300px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .meal-card h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .meal-card p {
    font-size: 16px;
    color: #555;
  }
  
  .meal-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
    margin-bottom: 10px;
  }
  