.box {
    width:350px; /* Width of the box */
    height:250px; /* Height of the box */
    background-color: rgb(255, 255, 255); /* Background color */
    border:1px solid rgb(24, 24, 26); /* Border of the box */
    display: flex; /* Optional: for content alignment */
    justify-content: center; /* Center horizontally */
    align-items: center; /* Center vertically */
    color: white; /* Text color */
    font-size: 16px; /* Text size */
    text-align: center; /* Text alignment */
    position: relative;
    margin-top:-350px;
    margin-left:180px;
    
  }
  
  .box p
  {
    color: grey;
    font-weight: 900;
    margin-left:10px;
    margin-top:-210px;
    margin-bottom: -35px;
  }
  .box h1
  {
    color: rgb(0, 0, 0);
    font-weight: 900;
    margin-left:-120px;
    margin-top:-80px;
    font-size:30px;
    margin-bottom: -50px;
  }
  .box h4
  {
    font-size:15px;
    color: black;
    margin-top:140px;
    margin-left:-290px;
    text-align: center;
    

  }
  .footer {
    background-color: #333; /* Background color */
    color: white; /* Text color */
    padding: 20px 0; /* Padding for top and bottom */
    text-align: center; /* Center text */
    margin-top:300px;
  }
  
  .footer-container {
    max-width: 1200px; /* Limit the width */
    margin: 0 auto; /* Center the container */
  }
  
  .footer-links {
    margin-top:10px;
    
  }
  .footer-links a {
    color: #aaa; /* Link color */
    margin: 0 10px; /* Spacing between links */
    text-decoration: none; /* Remove underline */
    transition: color 0.3s; /* Smooth color transition */
    margin-bottom: -150px;
  }
  
  .footer-links a:hover {
    color: white; /* Change color on hover */
  }
  
