.kitchen-tips-page {
    padding: 20px;
    background-color: #f4f4f4;
  }
  
  .kitchen-tips-page h1 {
    text-align: center;
    font-size: 36px;
    color: #333;
  }
  
  .kitchen-tips-page p {
    text-align: center;
    font-size: 18px;
    color: #555;
    margin-bottom: 20px;
  }
  
  .kitchen-tips-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px;
  }
  
  .kitchen-tip-card {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    width: 300px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .kitchen-tip-card h2 {
    font-size: 22px;
    margin-bottom: 10px;
  }
  
  .kitchen-tip-card p {
    font-size: 16px;
    color: #666;
  }
  
  .tip-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
    margin-bottom: 10px;
  }
  