/* Register.css */

/* Make sure the body takes up full height */
html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    
    
  
  }
  
  
  
  /* Ensure the register container is positioned above the video */
  .register-container {
    position: relative;
    z-index: 1; /* Bring the registration form above the video */
    padding:20px;
    margin: 50px auto;
    text-align: center;
    
  }
  
  /* Style the form container */
  .register-form {
    max-width:500px;
    margin: 0 auto;
    background-color: rgba(255, 255, 255, 0.8); /* Semi-transparent background */
    padding:20px;
    border-radius: 8px;
  }
  
  /* Style for the form fields */
  input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  /* Style for the submit button */
  button {
    
    padding: 10px;
    background-color: #de6f00;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  
  
  