nav {
    display: flex; /* Aligns items in a row */
    justify-content: space-between; /* Creates space between the title and the navbar */
    align-items: center; /* Centers items vertically */
    padding: 10px 20px; /* Adds some padding */
    background-color: #f8f8f8; /* Optional: Sets a background color */
    border-bottom: 2px solid #ddd; /* Optional: Adds a border for styling */
  }
  
  nav h1 {
    margin: 0; /* Removes default margin from the title */
    font-size: 24px; /* Adjusts title size */
    font-weight: bold; /* Makes the title bold */
  }
  
  nav ul {
    list-style: none; /* Removes the dots */
    padding: 0; /* Removes default padding */
    margin: 0; /* Removes default margin */
    display: flex; /* Aligns items in a row */
    margin-left: auto; /* Pushes the navbar to the right side */
  }
  
  nav ul li {
    margin-left: 20px; /* Adds spacing between links */
  }
  
  nav ul li a {
    text-decoration: none; /* Removes underline from links */
    color: black; /* Sets link color */
    font-weight: bold; /* Makes text bold */
  }
  
  nav ul li a:hover {
    color: blue; /* Adds hover effect */
  }

