.about-us-page {
    font-family: 'Arial', sans-serif;
    padding: 20px;
    background-color: #f9f9f9;
    color: #333;
  }
  
  .hero-section {
    text-align: center;
    padding: 40px 20px;
    background-color: #0e3fe0;
    color: white;
    border-radius: 8px;
  }
  
  .hero-section h1 {
    font-size: 48px;
    margin-bottom: 10px;
  }
  
  .hero-section p {
    font-size: 20px;
    color: white;
  }
  
  .about-us-content {
    margin-top: 20px;
  }
  
  .about-section {
    margin-bottom: 30px;
    color: black;
  }
  
  .about-section h2 {
    font-size: 28px;
    color: #ffffff;
    margin-bottom: 10px;
  }
  
  .about-section p {
    font-size: 18px;
    line-height: 1.6;
  }
  
  .about-section ul {
    list-style-type: disc;
    margin-left: 20px;
  }
  
  .about-section ul li {
    font-size: 18px;
  }
  
  .team-section {
    margin-top: 30px;
  }
  
  .team {
    display: flex;
    gap: 20px;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .team-member {
    text-align: center;
  }
  
  .team-member img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .team-member h3 {
    margin: 10px 0 5px;
    font-size: 20px;
    color: #000000;
  }
  
  .team-member p {
    font-size: 16px;
  }
  
  .about-us-footer {
    text-align: center;
    margin-top: 30px;
    padding: 20px;
    background-color: #4c4b4b;
    border-radius: 8px;
    font-size: 16px;
  }
  